* {
  margin: 0;
  scroll-behavior: smooth;
}
/* Chrome safari firefox */
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.greenColor {
  color: green;
}
.redColor {
  color: red;
}

.container-product {
  display: flex;
  margin: 2vmax auto;
  width: 80vw;
  flex-wrap: wrap;
  justify-content: center;
}
.feature-title {
  display: flex;
  justify-content: center;
  text-transform: capitalize;
}

.productCard {
  width: 17vmax; /* 14vmax */
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 2vmax;
  transition: al 0.5s;
  padding-bottom: 0.5vmax;
}
.productCard > img {
  width: 17vmax; /* 14vmax */
  height: 20vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
}
.productCard > div {
  display: flex;
  justify-content: flex-start;
  font-size: 0.7vmax;
}
.productCard > div > span {
  display: flex;
  flex-direction: row;
  margin: 0.5vmax;
  font: 400 0.5vmax "Arial", "sans-serif";
  font-size: 1vmax;
}
.productCard > p {
  font-family: "Arial", "sans-serif";
  font-size: 1.5vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}
.productCardSpan {
  font: 400 1vmax "Roboto";
  width: 20%;
}

.productCard > span {
  margin: 0.5vmax;
  color: tomato;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.8vmax;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
  -webkit-transform: translateY(-1vmax);
          transform: translateY(-1vmax);
  transition: 100ms ease-in-out;
}
.ratingStar {
  color: rgb(255, 190, 13);
  font-family: "Arial", serif;
  font-size: 1.5vmax;
  font-weight: bold;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

@media screen and (max-width: 600px) {
  .container-product {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .productCard > p {
    font-size: 1.7vmax;
  }
  .productCard > div {
    margin: 0vmax;
    display: block;
  }
  .productCard > span {
    font-size: 1.54vmax;
  }
  .productCard > div > span {
    margin: 0 0.5vmax;
    font: 300 1vmax "Roboto";
  }
}

.loading {
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  display: grid;
  place-items: center;
  max-width: 100%;
}
.loading > div {
  width: 10vmax;
  height: 10vmax;
  border-bottom: 5px solid rgb(102, 2, 2);
  border-radius: 50%;
  -webkit-animation: loadingRotate 1000ms linear infinite;
          animation: loadingRotate 1000ms linear infinite;
}
@-webkit-keyframes loadingRotate {
  to {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
  }
}
@keyframes loadingRotate {
  to {
    -webkit-transform: rotateZ(-360deg);
            transform: rotateZ(-360deg);
  }
}

