/*--------------------------------------------------------------------- File Name: responsive.css ---------------------------------------------------------------------*/

@media (min-width: 1200px) and (max-width: 1320px) {
  .about_box_img {
    margin-right: -20px;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  ul.email li {
    padding: 0 9px;
  }
  .banner_main .bg_white h1 {
    font-size: 60px;
  }
  .carousel-indicators {
    margin-left: 12%;
  }
  .about_box_img {
    margin-right: 35px;
  }
  .about_box_text p {
    font-size: 17px;
    line-height: 26px;
  }
  .margin_left60 {
    margin-left: -80px;
    margin-right: 80px;
  }
  .instant .titlepage h2 {
    font-size: 69px;
    line-height: 83px;
  }
  .reader h3 {
    font-size: 26px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
